/* global WE_API, MPP_API */
/**
 * This is the receiver installed in the enclosing window of the ContentCreator.
 *
 * This should not be integrated in the Third Party App. It is part of the
 * ThirdPartyPreview module and is injected in the ContentCreator application.
 * FRONTEND_VERSION = 2.4.1
 * GIT_HASH = 5267befed395792fb1ab5be6fbbd06667e5836e1
 */
(function () {
    'use strict';

    // Polyfill for Element.closest @ IE9+ by MDN
    if (!Element.prototype.matches) {
      Element.prototype.matches = Element.prototype.msMatchesSelector || Element.prototype.webkitMatchesSelector;
    }
    if (!Element.prototype.closest) {
      Element.prototype.closest = function(s) {
        var el = this;
        if (!document.documentElement.contains(el)) return null;
        do {
          if (el.matches(s)) return el;
          el = el.parentElement || el.parentNode;
        } while (el !== null && el.nodeType === 1);
        return null;
      };
    }

    // Hide undo / redo buttons in CC toolbar with new design
    var _hideUndoRedoButtons = function() {
      if (window['CxtHost']) {
        try {
          window['CxtHost'].client.shadowRoot.querySelector('cc-toolbar-undo-redo').style.display = "none";
        } catch (e) {
          console.debug("cannot hide undo/redo toolbar", e);
        }
      }
    }

    var tryApply = function (f, thisArg, args) {
      if (typeof f === 'function') {
        f.apply(thisArg, args);
      }
    };

    var PreviewScope = (function(){
      var _language = null;
      var _element = null;

      function _normalize(fsid) {
        if (!fsid) {
          return { id: -1, store: 'sitestore', language: _language };
        } else if ('id' in fsid) {
          return fsid;
        } else {
          return {
            store: fsid.getStoreType(),
            language: fsid.getLanguage() || _language,
            id: fsid.getId(),
            contentId: fsid.getContentId(),
            template: fsid.getTemplate()
          };
        }
      }

      function _isActive(fsid) {
        var element = _normalize(fsid);
        if (element.store.toLowerCase() != _element.store.toLowerCase()) return false;
        if ('contentId' in _element && _element.contentId != -1) {
          return element.contentId == _element.contentId && element.template == _element.template;
        } else {
          return element.id == _element.id;
        }
      }

      function _perform(isDefaultPeriod) {
        //the contentId.toString() conversion is needed because of a bug in ContentCreator: https://projects.e-spirit.de/browse/CORE-11618
        var strContentId = (_element && typeof _element.contentId === "number") ? {contentId: _element.contentId.toString()} : {};
        var bugfixedElement = Object.assign({}, _element, strContentId);

        WE_API.Common.setPreviewElement(bugfixedElement);
      }


      return {
        getLanguage: function() {
          return _language;
        },
        update: function(fsid) {
          _element = _normalize(fsid);
          _language = _element.language || _language;
          _perform();
        },
        refresh: function(fsid) {
          if (!fsid || _isActive(fsid)) {
            _perform();
          }
        }
      };
    })();

    var fsidToPreviewId = function (fsid, callback) {
      if (fsid) {
        if (fsid.getElementType() === 'CS_DATASET') {
          WE_API.Common.execute('class:TppApi', {
            action: 'FSID_TO_PREVIEW_ID',
            language: fsid.getLanguage() || PreviewScope.getLanguage(),
            template: fsid.getTemplate().toFixed(0),
            store: fsid.getStoreType(),
            contentId: fsid.getId().toFixed(0)
          }, callback);
        } else {
          WE_API.Common.execute('class:TppApi', {
            action: 'FSID_TO_PREVIEW_ID',
            language: fsid.getLanguage() || PreviewScope.getLanguage(),
            id: fsid.getId().toFixed(0),
            store: fsid.getStoreType()
          }, callback);
        }
      } else {
        tryApply(callback, this, [null]);
      }
    };

    var getElementStatus = function (previewId, callback, onError) {
      if (previewId) {
        WE_API.Common.execute("class:TppApi", {
          action: "STATUS",
          previewId: previewId
        }, function (response) {
          handleApiError(response);
          var jsonResponse = tryParseResponse(response);
          if (!jsonResponse.error) {
            tryApply(callback, this, [jsonResponse.object]);
          } else {
            tryApply(onError, this, [jsonResponse]);
          }
        });
      } else {
        tryApply(onError, this, [{error: true, object: "No previewId given in getElementStatus in receiver"}])
      }
    };

    var previewIdToFsId = function (previewId, callback, onError) {
      getElementStatus(previewId, function (status) {

        // in case the element was deleted we are unable to determine the status,
        // so we call onError and prevent further execution and follow-up errors.
        // see: com.espirit.tpp.api.element.impl.StoreElementInfoImpl.unknownElement
        if("unknown" === status.name && !status.id) {
          tryApply(onError, this, [{error: false, previewId: null}]);
          return;
        }

        var fsid;
        if (status.templateId) {
          fsid = {
            contentId: +status.entityId, // mandantory for dataset
            template: +status.templateId, // template node id
            id: status.entityId,
            store: status.storeType,
            language: status.language
          };
        } else {
          fsid = {
            id: status.id,
            store: status.storeType,
            language: status.language
          };
          if (+status.entityId !== -1) {
            fsid.contentId = +status.entityId;
            fsid.pageref = +status.status.id;
          }
        }
        tryApply(callback, this, [fsid]);
      }, onError);

    };

    var tryParseResponse = function (response, defaultValue) {
      if (typeof(response) === 'string' && response.match(/^\s*[\[{"]/) !== null) {
        try {
          return JSON.parse(response);
        } catch (ignore) {
          return (typeof defaultValue === 'undefined') ? null : defaultValue;
        }
      }
    };

    var showInvalidResponseMessage = function (response) {
      console.log("Invalid response: ", response);
      WE_API.Common.showMessage("Invalid response", "An invalid response was received from TppApi-Service. See console for details");
    };

    var showErrorMessage = function (jsonResponse) {
      console.log(jsonResponse.object.stacktrace);
      var hasTitle = jsonResponse.object.localizedTitle.length > 0;
      var hasMessage = jsonResponse.object.localizedMessage.length > 0;
      WE_API.Common.showMessage(hasTitle ? jsonResponse.object.localizedTitle : "Unknown error", hasMessage ? jsonResponse.object.localizedMessage : "An unknown error occured. See console for details.");
    };

    // This just checks if there might be an TppApi response that is marked as an error
    // Non-TppApi responses will be ignored
    var handleApiError = function (response) {
      var jsonResponse = tryParseResponse(response);
      if (jsonResponse && jsonResponse.error) {
        showErrorMessage(jsonResponse);
      }
    };

    //This assumes you have a valid TppApi response and stops by not calling the callback
    var handleResponse = function (response, callback) {
      var jsonResponse = tryParseResponse(response);
      if (jsonResponse === null) {
        showInvalidResponseMessage(response)
      } else if (jsonResponse.error) {
        showErrorMessage(jsonResponse);
      } else {
        tryApply(callback, this, [jsonResponse.object]);
      }
    };


    let displayChangesRequestActive = false;

    /**
     * Post Event-Messages from outer CC-Frame to inner Third Party Frame.
     * These messages weren't requested by any direct message from the Third Party Frame.
     */
    var eventDispatcher = function () {
      var target = null;
      var initialPreviewElement;
      var postEventMessage = function (eventId, payload) {
        var tppMessage = {
          _messageType: "EVENT",
          _eventId: eventId,
          _payload: payload
        };
        if (target) {
          target.window.postMessage({tpp: tppMessage}, target.origin);
        }
      };

      var previewRequestHandler = function (fsid) {
        if (fsid === null) return; // Ignore invalid fsids
        // user clicked on search result --> navigate to structure node
        // TODO: retrieve the actual element type by "getElementStatus(fsidToPreviewId(...))" to match all cases?
        // CORE-12269 Allowing null element type due to a bug: When a deep link is provided through the respective
        // query params (element, store) the element type of fsid is null.
        if (fsid.getElementType() === 'SS_PAGE' || fsid.getElementType() === 'CS_DATASET' || fsid.getElementType() === null) {
          // Delaying delegation of preview request event for initially requested element when preview frame hasn't
          // connected yet by temporarily caching initially requested element.
          if(!target && initialPreviewElement === undefined) {
            initialPreviewElement = fsid;
            return;
          }
          fsidToPreviewId(fsid, function (response) {
            handleApiError(response);
            postEventMessage("TPP_PREVIEW_REQUEST", response);
          });
        }
      };

      WE_API.Common.addPreviewRequestHandler(previewRequestHandler);

      WE_API.Common.addNavigationChangeListener(function (fsid) {
        if (fsid === null) {
          //case 1. Folder is moved in navigation
          postEventMessage("TPP_NAVIGATION_CHANGED", {error: false, object: null});
        } else {
          //case 2. New Page/PageRef was created
          fsidToPreviewId(fsid, function (response) {
            handleApiError(response);
            postEventMessage("TPP_NAVIGATION_CHANGED", response);
          });
        }
      });

      WE_API.Common.addItemsPlugin("status", function clientItemsPlugin(context, receiver) {
        var items = [];

        if(displayChangesRequestActive) {
          const title = WE_API.Common.getLocale() === "de" ? "Änderungen ausblenden" : "Hide changes";
          items.push(context.createItem(null, title, function clientItemPerformable() {
            postEventMessage("TPP_RESET_DISPLAY_CHANGES_REQUEST");
            displayChangesRequestActive = !displayChangesRequestActive;
            PreviewScope.refresh();
          }));
        } else {
          const title = WE_API.Common.getLocale() === "de" ? "Alle Änderungen anzeigen" : "Display changes";
          items.push(context.createItem(null, title, function clientItemPerformable() {
            postEventMessage("TPP_DISPLAY_CHANGES_REQUEST");
            displayChangesRequestActive = !displayChangesRequestActive;
            PreviewScope.refresh();
          }));
        }

        receiver(items);
      });

      var buildWorkflowMessage = function (workflowInfo, previewId) {
        var message = {};
        message.error = false;
        message.object = {
          previewId: previewId,
          workflowId: workflowInfo.getWorkflowId(),
          transitionId: workflowInfo.getTransitionId(),
          isFirstTransition: workflowInfo.isFirstTransition(),
          isEndState: workflowInfo.isEndState(),
          isDeleted: workflowInfo.isDeleted(),
          isReleased: workflowInfo.isReleased()
        };
        return message;
      };

      WE_API.Common.addWorkflowTransitionListener(function (workflowInfo) {
        var fsid = workflowInfo.getWorkflowTarget();
        PreviewScope.refresh(fsid);
        fsidToPreviewId(fsid, function (previewIdResponse) {
          handleResponse(previewIdResponse, function (previewId) {
            var message = buildWorkflowMessage(workflowInfo, previewId);
            postEventMessage("TPP_WORKFLOW_ACTION", message);
            PreviewScope.refresh();
          });
        });
      });

      // Support drag & drop over iframe
      var $iframe = document.getElementById('previewContent');
      if ($iframe !== null) {
        document.body.addEventListener('dragstart', function(e) {
          // skip behavior for crop-dialog, because 'dragend' will never be triggered
          if (e.target.closest('.fs-CropBox') !== null) return;

          $iframe.style.pointerEvents = 'none';
        }, true);
        document.body.addEventListener('dragend', function(e) {
          $iframe.style.removeProperty('pointer-events');
        }, true);
      }

      // Report: drag & drop support
      var $sidebar = document.querySelector('.fs-sidebar-content');
      if ($sidebar !== null) {
        $sidebar.addEventListener('dragstart', function(e, $item) {
          if (document.querySelector('.gwt-PopupPanelGlass') !== null) return;

          function tppDrag($source, previewId) {
            var rect = document.getElementById('previewContent').getBoundingClientRect();
            var $dropzone = document.body.appendChild(document.createElement('div'));
            $dropzone.setAttribute('style', 'position:absolute;z-index:10;overflow:auto;top:'+rect.top+'px;left:'+rect.left+'px;width:'+rect.width+'px;height:'+rect.height+'px;');

            function sendDragEvent(phase, x, y) {
              postEventMessage('TPP_DRAG_ACTION', {
                error: false,
                object: { previewId: previewId, phase: phase, x: x || -1, y: y || -1 }
              });
            }

            sendDragEvent('start');

            $dropzone.addEventListener('dragover', function(e){
              e.preventDefault();
              sendDragEvent('drag', e.offsetX, e.offsetY);
            });

            $dropzone.addEventListener('drop', function(e){
              e.preventDefault();
              sendDragEvent('drop', e.offsetX, e.offsetY);
            });


            var clean = function() {
              sendDragEvent('end');
              document.body.removeChild($dropzone);
              $source.removeEventListener('dragend', clean);
            };

            $source.addEventListener('dragend', clean);
          }

          var $source = e.target;
          if (($item = $source.closest('[fsid]')) !== null) {
            var fsid = $item.getAttribute('fsid').split(':');

            if (fsid[0] === 'FSID') {
              var attr = {};
              fsid[2].split(';').forEach(function(pair){
                if (pair = pair.match(/^([^=]*)=(.*)$/)) {
                  attr[pair[1]] = pair[2];
                }
              });

              var identifier = null;
              if (typeof(attr.tbl) !== 'undefined') {
                identifier = { template: attr.tbl, contentId: attr.id, store: attr.stype };
              } else {
                identifier = { id: attr.id, store: attr.stype };
              }

              if (identifier !== null) {
                identifier.store = ['PAGESTORE','CONTENTSTORE','SITESTORE','MEDIASTORE','TEMPLATESTORE','GLOBALSTORE'][+identifier.store];
                identifier.action = 'FSID_TO_PREVIEW_ID';
                identifier.language = PreviewScope.getLanguage();
                WE_API.Common.execute('class:TppApi', identifier, function(result) {
                  handleResponse(result, function(previewId) {
                    tppDrag($source, previewId);
                  });
                });
              }
            }

          } else if (($item = $source.closest('[data-identifier]')) !== null) {
            var $report = document.querySelector('.fs-sidebar-button.pressed[data-fs-element-name]');
            if ($report !== null) {
              tppDrag($source, 'custom:dap:' + $report.getAttribute('data-fs-element-name') + ':' + $item.getAttribute('data-identifier'));
            }
          }
        });
      }

      MPP_API.addParameterizedListener((value) =>
        postEventMessage("TPP_MPP_CHANGE", { type: "onMppParameterizedChange", result: value })
      );
      MPP_API.addParameterListener(() =>
        postEventMessage("TPP_MPP_CHANGE", { type: "onMppParameterChange", result: undefined })
      );
      MPP_API.addTimeParameterListener((date) =>
        postEventMessage("TPP_MPP_CHANGE", { type: "onMppTimeParameterChange", result: date })
      );

      return {
        connectApi: function (registerEvent) {
          if (registerEvent) {
            target = {
              window: registerEvent.source,
              origin: registerEvent.origin
            };
            if(initialPreviewElement) {
              previewRequestHandler(initialPreviewElement);
              initialPreviewElement = null;
              // Hide undo / redo buttons in CC toolbar with new design
              _hideUndoRedoButtons();
            }
          }
        },
        postEventMessage
      }
    }();


    /**
     * Handle messages/calls from the inner Third Party Preview frame (fs-tpp-api).
     */
    window.addEventListener('message', function (e) {
      if( !window.TEST_MODE && !window.CxtHost.client.shadowRoot.querySelector('cc-preview').shadowRoot.querySelector('#previewContent').getAttribute("src").startsWith(e.origin) ) {
        return;
      }

      var postCallbackMessage = function (response, params) {
        e.source.postMessage({
                               tpp: {
                                 _messageType: "CALLBACK",
                                 _callbackId: e.data.tpp._callbackId || null,
                                 _response: response,
                                 _params: params || null
                               }
                             }, e.origin);
      };


      if (typeof(e.data) === 'object' && typeof(e.data.tpp) === 'object') {

        if (e.data.tpp.execute) {
          if (e.data.tpp.execute.source) {
            // wrapper TPP_SNAP.execute -> execute function in scope of CC
            var f = new Function("return " + e.data.tpp.execute.source)();
            var response = f.apply(null, e.data.params);
            postCallbackMessage(response);
          } else {
            // wrapper TPP_SNAP.execute -> WE_API.Common.execute
            if (window['CxtHost'] !== undefined) {
              var $iframe = document.getElementById("previewContent");
              if ($iframe === null) {
                $iframe = window['CxtHost'].client.shadowRoot.querySelector("cc-preview").shadowRoot.querySelector("#previewContent");
              }
              if (e.data.tpp.params.boundX !== undefined) {
                e.data.tpp.params.boundX += $iframe.getBoundingClientRect().x;
              }
              if (e.data.tpp.params.boundY !== undefined) {
                e.data.tpp.params.boundY += $iframe.getBoundingClientRect().y;
              }
            }
            WE_API.Common.execute(e.data.tpp.execute, e.data.tpp.params, function (response) {
              handleApiError(response);
              postCallbackMessage(response);
            });
          }
        }

        if (e.data.tpp.connectApi) {
          eventDispatcher.connectApi(e);
          postCallbackMessage({error: false, message: "successfull connected"});
        }

        if (e.data.tpp.ping) {
          // TODO: no official API; rather change this as soon as there's a real API - or remove if legacy CC is no longer supported
          var isLegacyCC = !window.CxtHost;
          postCallbackMessage({error: false, pong: true, version: '2.4.1', isLegacyCC: isLegacyCC});
        }

        if (e.data.tpp.hideChanges) {
          displayChangesRequestActive = false;
          PreviewScope.refresh();
        }

        if (e.data.tpp.setPreviewElement) {
          var previewId = e.data.tpp.previewId;
          if (previewId) {
            previewIdToFsId(previewId, function (fsid) {
              PreviewScope.update(fsid);
              postCallbackMessage({error: false, previewId: previewId})
            }, function (errorResponse) {
              postCallbackMessage(errorResponse);
            });
          } else if (previewId === null) {
            PreviewScope.update(null);
            postCallbackMessage({error: false, previewId: null});
          } else {
            // Trigger refresh
            PreviewScope.refresh();
            postCallbackMessage({error: false, previewId: null});
          }
        }

        if (e.data.tpp.mpp) {
          postCallbackMessage(MPP_API[e.data.tpp.mpp.method].apply(MPP_API, e.data.tpp.mpp.args||[]));
        }
      }
    });
  }
)();
